.nav-btns {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: flex-start;
  grid-gap: 15px;
  margin: 50px 0px 75px 0px;
}

.nav-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  padding: 8px 14px;
  color: #5983fc;
  border: 1px solid #5983fc;
  border-radius: 3px;
  cursor: pointer;
  transition: color 0.3s linear;
  transition: Background 0.3s linear;
}

.nav-btn:hover {
  background: #5983fc;
  color: white;
  transition: 0.3s linear;
}

.active-btn {
  background: #5983fc;
  color: white;
}

.container {
  display: grid;
  grid-template-columns: 0.5fr 0.7fr 0.7fr 0.6fr 2fr 2fr 0.3fr;
  grid-template-rows: 0.3fr 1.7fr 0.6fr 0.5fr 0.5fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas: ". . . . . . ." ". form form . relust relust ." ". form form . relust relust ." ". form form . relust relust ." ". . . . . . ." ". . . . css-kod css-kod ." ". . . . . . .";
}

.form {
  grid-area: form;
}

.boxRelust {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: relust;
  overflow: hidden;
}

.css-kod {
  grid-area: css-kod;
}

textarea {
  width: 95%;
  resize: none;
  height: 100px;
  background-color: #1f2024;
  border-radius: 12px;
  color: #fff;
  font-family: inherit;
  font-size: 14px;
  overflow-x: hidden;
  padding: 20px;
}

.box-copy {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  margin-left: 16px;
  font-family: sans-serif;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 470px;
}

.box-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0px 3px;
}

#resultTransform {
  width: 250px;
  height: 250px;
  background-color: #5983fc;
  border-radius: 12px;
}

.mini-btn {
  background-color: #5983fc;
  border: 2px solid #5983fc;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: Roboto Mono, monospace;
  font-size: 14px;
  padding: 4px 12px;
  position: relative;
  transition: all 0.2s ease-out;
}

#copy-btn:hover {
  opacity: 0.8;
}

#transitionForm label {
  margin: 20px 0px;
}

select {
  border: none;
  border-bottom: 1px solid #5983fc;
  width: 80px;
}

#resultTransition {
  width: 250px;
  height: 250px;
  background-color: #5983fc;
  border-radius: 12px;
  color: white;
  border: 5px solid #5983fc;
}

#resultTransition span {
  margin: 90px 0px 0px 0px;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  font-family: sans-serif;
}

/* flexboxForm */
#flexboxForm .box-label {
  margin: 20px 0px;
}

.box-label-uni {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0px 0px 0px;
  grid-gap: 10px;
}

.btnAR {
  width: 100%;
  height: 30px;
  border: none;
  border: 1px solid #5983fc;
  border-radius: 5px;
  background: rgba(43, 128, 255, 0.3);
  transition: 0.3s linear;
}

.btnAR:hover {
  background: none;
  transition: 0.3s linear;
}

.flexBoxRelust {
  width: 100%;
  height: 100%;
}

#a {
  width: 100%;
  height: 100%;
  grid-area: relust;
}

#mainBoxResult {
  display: inline-flex;
  border: 3px dashed #5983fc;
  border-radius: 10px;
  padding: 10px;
  grid-gap: 5px;
  grid-area: relust;
  height: 100%;
}

.resultFlexBox {
  width: 50px;
  height: 50px;
  background-color: #5983fc;
  border-radius: 12px;
  color: white;
  border: 5px solid #5983fc;
}

#flexBoxCSS {
  font-size: 14px;
}

